import List from 'base/list/list'
import { ERR_OK } from 'api/config'
import { listLoadMixin } from 'common/js/mixin'
import VHeader from 'components/v-header/v-header'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { mapGetters, mapActions, mapMutations } from 'vuex'
import { getIndustryTrend, getCompanyTrend, getProductTrend, getTenderTrend, getIndustryClassify, getCompanyClassify, getProductClassify, getTenderClassify } from 'api'

export default {
  name: 'dynamic',
  mixins: [listLoadMixin],
  data () {
    return {
      swiperOption: {
        slidesPerView: 3,
        paginationClickable: true,
        spaceBetween: 20,
        freeMode: true
      },
      list: [],
      tabs: [],
      page: 1,
      hasMore: true,
      hasRefresh: true,
      pullup: true,
      pulldown: true,
      reveal: false
    }
  },
  computed: {
    ...mapGetters([
      'discoveryItem'
    ])
  },
  components: {
    List,
    swiper,
    VHeader,
    swiperSlide
  },
  watch: {},
  methods: {
    getDetail (item) {
      this.setDynamicItem({ tag: item.tag.toString(), title: item.name })
      this.$router.push({
        path: `/discovery/${this.$route.params.item}/${item.id}`
      })
    },
    async _getIndustryClassify () {
      const params = {
      }
      return await getIndustryClassify(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getCompanyClassify () {
      const params = {
      }
      return await getCompanyClassify(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getProductClassify () {
      const params = {
      }
      return await getProductClassify(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getTenderClassify () {
      const params = {
      }
      return await getTenderClassify(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getIndustryTrend (params) {
      return await getIndustryTrend(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getCompanyTrend (params) {
      return await getCompanyTrend(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getProductTrend (params) {
      return await getProductTrend(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getTenderTrend (params) {
      return await getTenderTrend(params).then(data => {
        if (data.code === ERR_OK) {
          return data
        }
      })
    },
    async _getClassify (item) {
      switch (item) {
        case 'industry': {
          return await this._getIndustryClassify()
        }
        case 'company': {
          return await this._getCompanyClassify()
        }
        case 'product': {
          return await this._getProductClassify()
        }
        case 'tender': {
          return await this._getTenderClassify()
        }
        default: {
          return await ''
        }
      }
    },
    async _getTrend (item) {
      const params = {
        limit: 20,
        currrentPage: this.page
      }
      switch (item) {
        case 'industry': {
          return await this._getIndustryTrend(params)
        }
        case 'company': {
          return await this._getCompanyTrend(params)
        }
        case 'product': {
          return await this._getProductTrend(params)
        }
        case 'tender': {
          return await this._getTenderTrend(params)
        }
        default: {
          return await ''
        }
      }
    },
    loadMore () {
      this.page += 1
      this.hasMore = true
      this._getTrend(this.discoveryItem).then(data => {
        if (data.code === ERR_OK) {
          this.list = this.list.concat(data.list)
        }
      })
    },
    refresh () {
      this.page = 1
      this.reveal = true
      this._getTrend(this.discoveryItem).then(data => {
        if (data.code === ERR_OK) {
          this.reveal = false
          this.list = data.list
        }
      })
    },
    async _concurrent () {
      if (!this.discoveryItem) this.$router.push('/discovery')
      const data = await Promise.all([this._getClassify(this.discoveryItem), this._getTrend(this.discoveryItem)])

      const [{ list: tabs }, { list }] = data

      this.tabs = tabs
      this.list = list

      return data
    },
    getTitle (label) {
      switch (label) {
        case 'industry': {
          return ''
        }
        case 'company': {
          return ''
        }
        case 'product': {
          return ''
        }
        case 'tender': {
          return ''
        }
        default: {
          return ''
        }
      }
    },
    selectItem (item) {
      this.fetchDetail(item)
    },
    ...mapActions([
      'fetchDetail'
    ]),
    ...mapMutations({
      'setDynamicItem': 'SET_DYNAMIC_ITEM'
    })
  },
  activated () {
  },
  created () {
    this._concurrent()
  },
  mounted () {}
}



